import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';

export class AddTodo extends Component {
    state ={
        title: ''
    }

    onChange = (e) => this.setState({
        [e.target.name]: 
        e.target.value.trim()});
        
    onSubmit = (e) => {
        const data = e.target.title.value.trim();
        if (data === ""){
            confirmAlert({
                message: 'Todos text box can not be empty',
                buttons: [
                    {
                        label: 'Close Me'
                    }
                ]
            });
        }
        e.target.title.value=''
        e.preventDefault();
        this.props.addTodo(this.state.title);
        this.setState({title: ''});
    }


    render() {
        return (
            <div>
                <form id='textForm' onSubmit={this.onSubmit} style={{ display: 'flex'}}>
               <input type = 'text' 
                      name ='title' 
                      autoFocus
                      style ={{flex: '10' , padding: '15px' , fontSize: '15px'}}
                      placeholder = 'Enter to Add Todos ...'
                      value={this.state.value}
                      onChange ={this.onChange}
                />
                <input type = 'submit' 
                value = 'Add Todo' 
                className = 'btn'
                style ={{flex: '1'}}   
                />
           </form>
            </div>
        )
    }
}

AddTodo.propTypes = {
    addTodo: PropTypes.func.isRequired,
}

export default AddTodo;
