import React, { Component } from 'react'
import PropTypes from 'prop-types';
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

export class TodoItem extends Component {

    constructor(props) {
        super(props);
        this.state = {
            title: props.todo.title,
            id: props.todo.id
        };
    }

    getStyle = ()  => {
            return {
                background: this.props.todo.isEditMode ? '#FFFFE0' : '#fff',
                padding: '16px 20px',
                borderBottom: '1px  solid',
                textDecoration: this.props.todo.complete ? 'line-through' : 'none'
            } 
    }  

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
        console.log(e.target.value)
    }

    onCancel = (e) => {
        console.log("onCancel" + this.props.todo.title)
        e.preventDefault();
        this.setState({
            title: this.props.todo.title
        })
        this.props.changeCancelMode(this.props.todo.id)
    }

    onSubmit = (e) => {
        e.preventDefault();
        const form = {
            title: this.state.title,
            id: this.state.id
        }
        this.props.updateTodoTitle(form)
    }


    render() {
        return (
            this.props.todo.isEditMode ?
            <div style = {this.getStyle()}>
                            <form>
                            <p>
                                <input type='text'    
                                    name='title'
                                    style={inputText}
                                    value={this.state.title}
                                    onChange={e => this.handleChange(e)}
                            />&nbsp;&nbsp;
                                <button onClick={(e) => this.onSubmit(e)} className='save'>Save Change</button>&nbsp;&nbsp;
                                <button onClick={(e) => this.onCancel(e)} className='cancel'>Cancel</button>

                            {/* <button onClick={this.props.changeCancelMode.bind(this, this.props.todo.id)} class='cancel'>Cancel</button> */}
                            </p>
                                </form>
            </div>
            :
                <div style={this.getStyle()} className = 'changeColor'>
                    <p className='changeColor'>
                         <input type='checkbox' onChange={this.props.markComplete
                            .bind(this, this.props.todo.id)} checked={this.props.todo.complete} /> &nbsp;&nbsp;
                        {this.props.todo.title} 
                        <button onClick={this.props.delTodo.bind(this, this.props.todo.id)} className='delete'>Delete</button> 
                      <button onClick={this.props.changeEditMode.bind(this, this.props.todo.id)} className='edit'>Edit</button> 
                    </p>
                </div>
        )
    }
}
TodoItem.propTypes = {
    todo: PropTypes.object.isRequired,
    markComplete: PropTypes.func.isRequired,
    changeEditMode: PropTypes.func.isRequired,
    delTodo: PropTypes.func.isRequired,
    updateTodoTitle: PropTypes.func.isRequired,
    changeCancelMode: PropTypes.func.isRequired
}

const inputText = {
    width: '80%',
    padding: '5px 5px',
    display: 'inline-block',
    fontSize: '14px',
    borderColor: '#5c8a8a'
}

export default TodoItem
