import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Header from './component/layout/Header'
import Todos from './component/Todos';
import AddTodo from './component/AddTodo';
import uuid from "uuid";
import About from './component/pages/About';
import './App.css';
import axios from 'axios';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import Footer from './component/layout/Footer'

class App extends Component {
  state = {
    todos: []
  }

  //Confirmation popup
  clearConfirm = () => {
    confirmAlert({
      title: 'Confirm to submit',
      message: 'Are you sure to delete all the todos in list ?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => { 
          localStorage.clear();
          this.setState({ todos: [] });
        }
        },
        {
          label: 'No',
          onClick: () => {
          }
          
        }
      ]
    })
  };
  
  componentDidMount() {
    axios.get('https://jsonplaceholder.typicode.com/todos/?_limit=1')
    .then(response => {
      //this.setState({todos: response.data})
    })
  
    const result = [];
    if(localStorage.length>0){
      for (var i = 0; i < localStorage.length; i++) {
        var key = localStorage.key(i);
        var value = localStorage.getItem(key);
        var valueParse = JSON.parse(value)
        result.push(valueParse)
      }
    }   
    this.setState({ todos: result })
  }

  markComplete = (id) => {
    var value = localStorage.getItem(id);
    var parse = JSON.parse(value)
    parse.complete = ! parse.complete
    localStorage.setItem(id, JSON.stringify(parse))
    this.setState({ todos:  this.state.todos.map( todo => {
      if(todo.id === id){
        todo.complete = ! todo.complete
      }
    return todo
    })})
}

  editTitle = (id) => {
    var value = localStorage.getItem(id);
    var parse = JSON.parse(value)
    parse.complete = !parse.complete
    localStorage.setItem(id, JSON.stringify(parse))
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          todo.complete = !todo.complete
        }
        return todo
      })
    })
  }
  changeEditMode = (id) => { 
    console.log("Change edit mode") 
    var value = localStorage.getItem(id);
    var parse = JSON.parse(value)
    parse.isEditMode = ! parse.isEditMode
    console.log(parse.title)
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          console.log(todo.title)
          todo.isEditMode = !todo.isEditMode
          todo.title = parse.title
        }
        return todo
      })
    })
  }

  changeCancelMode = (id) => {
    console.log("changeCancelMode")
    var value = localStorage.getItem(id);
    var parse = JSON.parse(value)
    parse.isEditMode = false
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          todo.isEditMode = false
        }
        return todo
      })
    })
  }

//Delete Todo
delTodo = (id) => {
  console.log(id)
  // axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
  // .then(response =>   this.setState({todos: [...this.state.todos.filter(todo => todo.id !== id)]})
  // )
  localStorage.removeItem(id)
   this.setState({todos: [...this.state.todos.filter(todo => todo.id !== id)]})
}

//Add Todos
addTodo = (addTitle) => {
  if(addTitle){
    const newTodo = {
      id: uuid.v4(),
      title: addTitle,
      complete: false,
      isEditMode: false
    }
    console.log(JSON.stringify(newTodo))
    localStorage.setItem(newTodo.id, JSON.stringify(newTodo))
    this.setState({ todos: [...this.state.todos, newTodo] });  } 
  

  // axios.post(`https://jsonplaceholder.typicode.com/todos/`,{
  //   title: addTitle,
  //   complete: false
  // })
  // .then(response => this.setState({todos: [...this.state.todos, response.data]}));
}

  updateTodoTitle = (form) => {
    var id = form.id
    var value = localStorage.getItem(id);
    var parse = JSON.parse(value)
    parse.title =  form.title
    localStorage.setItem(id, JSON.stringify(parse))
    this.setState({
      todos: this.state.todos.map(todo => {
        if (todo.id === id) {
          todo.isEditMode = false
          todo.title = form.title
        }
        return todo
      })
    })
  }

  

  onSubmit = () => {
    if (localStorage.length > 0) {
      for (var i = 0; i < localStorage.length; i++) {
        var key = localStorage.key(i);
        this.delTodo(key)
      }
    } 
  }

//Add Todo

  render() {
  return (
    <Router>
      <div className="App">
        <div className = 'container'>
          <Header />
          <Route exact path='/mytodos/' render={pros => (
              <React.Fragment>
                      <AddTodo addTodo = {this.addTodo}/>
                        <Todos todos ={this.state.todos} 
                               markComplete = {this.markComplete}
                               delTodo = {this.delTodo} 
                               changeEditMode={this.changeEditMode}
                               updateTodoTitle={this.updateTodoTitle}
                               changeCancelMode={this.changeCancelMode}
                               />
              <input type='submit'
                onClick={this.clearConfirm}
                value='Clear All'
                className='btnclear'
                style={{ flex: '1' }}
              />
              </React.Fragment>
            
          )}/>
          <Route path='/mytodos/about' component={About}/>
          <Footer />
        </div>
        
      </div>
      </Router>
  );
}
}

export default App;
