import React, { Component } from 'react';
import TodoItem from './TodoItem';
import PropTypes from 'prop-types';
class Todos extends Component {
   
  render() {    
  return this.props.todos.map((todo) => (
      <TodoItem key ={todo.id} todo={todo} 
      markComplete = {this.props.markComplete}
      delTodo = {this.props.delTodo} 
      changeEditMode = {this.props.changeEditMode}
      updateTodoTitle={this.props.updateTodoTitle}
      changeCancelMode={this.props.changeCancelMode}
      />
  ))
}
}
 Todos.propTypes = {
     todos: PropTypes.array.isRequired,
     markComplete: PropTypes.func.isRequired,
     changeEditMode: PropTypes.func.isRequired,
     delTodo: PropTypes.func.isRequired,
   updateTodoTitle: PropTypes.func.isRequired,
   changeCancelMode: PropTypes.func.isRequired
 }
export default Todos;   
